import React, { useState } from "react";
import ReactDOM from "react-dom";

import "./styles.css";

//elemento
const elemento = (
  <div>
    <h3>Olá André</h3>
  </div>
); // JSX
// elemento2 em javascript puro
const elemento2 = React.createElement(
  "div",
  null,
  React.createElement("h4", null, "Olá André")
);

// forma alternativa de mostrar o valor de i
// no componente App

/*
function MostrarI(props){
  return <p>{props.i}</p>
}
ou com arrow function, conforme abaixo
*/

const MostrarI = (props) => {
  return <p>{props.i}</p>;
};

//componente funcional (functional component)
// o componente é uma estrutura que retorna elemento(s)
function App(props) {
  // cria uma variável i e cria uma função setI
  // toda vez que quiser ler ou alterar a variável i, usa-se a função setI
  // useState é um gancho ou um hook e neste caso inicializa i com o valor 1
  // o useState é uma foto que vou tirando da variável que quero armazenar
  // preferencialmente o useState() deve ser colocado no início do componente

  const [i, setI] = useState(1);

  // a construção abaixo, funcuiona também perfeitamente
  // const aux = useState(1)
  // const i = aux[0]
  // const setI = aux[1]

  //let i = 1
  const increment = () => {
    setI(i + 1);
  };
  return (
    <div className="App">
      <h1>Hello André</h1>
      <h2>
        Hello {props.name} {i}
      </h2>
      <button onClick={increment}>Incremento</button>
      <MostrarI i={i} />
      <h2>Start editing to see some magic happen!</h2>
      {elemento}
      {elemento2}
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App name="Crisb" />, rootElement);
